.thumb {
  width: 100%;
  height: 340px;
  background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.3) 5%,
      40%,
      transparent 90%
    )
    #ff6060;
  border-radius: 10px;
  color: #fff;
  font-size: 0.8rem;
  overflow: hidden;
  position: relative;
}

.thumb::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(transparent 60%, #000);
  opacity: 0.2;
  z-index: 1;
}

.thumb a {
  height: 100%;
}

.thumb h2 {
  margin: 1em;
  position: absolute;
  bottom: 0;
  z-index: 2;
}

.cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 500px) {
  .thumb {
    max-height: 255px;
  }
  .thumb h2 {
    left: 0;
    right: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
