.dropdown {
  margin: 2em 0;
  width: 45%;
}
.dropdown.large {
  width: 80%;
  max-width: 1000px;
}
.dropdown_button {
  padding: 1em;
  background-color: #ff6060;
  color: #fff;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  position: relative;
  border-radius: 10px;
  z-index: 2;
  font-weight: 500;
}

.arrow.active {
  transform: rotate(180deg);
}

.dropdown_content {
  padding: 1.5em 1em;
  background-color: #f6f6f6;
  display: none;
  opacity: 0;
  transition: 0.3s ease-in;
  position: relative;
  top: -5px;
  border-radius: 5px;
}

.dropdown_content.active {
  display: block;
  opacity: 1;
  transform: translateY(0px);
}

@media screen and (max-width: 980px) {
  .dropdown,
  .dropdown.large {
    width: 100%;
    margin: 1em 0;
  }
}
